h1 {
  margin: auto;
  padding: 20px;
  text-align: center;
  width: 100%;
  color: rgb(255, 255, 255);
  font-size: 60px;
}

p {
  padding-bottom: 2px;
}

ol {
  margin-left: -10px;
}

li {
  margin-left: -10px;
}

.hidden {
  visibility: hidden;
}

.form {
  width: calc(110% - (14 * 2vw));
  align-items: center;
  margin: auto;
  margin-top: 30px;
}

.input-container {
  justify-content: center;
  margin-top: -10px;
  margin-left: -15px;
  width: 100%;
  display: flex;
  padding-bottom: 260px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 10px;
  position: relative;
  background: rgba(197, 197, 197, 0.3);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.3);
  height: calc(120px + 350px - (3 * 10vw));
}

.identitas {
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  max-height: 50px;
  position: absolute;
}

.leftHalf {
  flex: 1;
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  max-height: 50px;
  margin-bottom: 140px;
  margin-top: 80px;
  justify-content: end;
  margin-right: 10px;
}

.rightHalf {
  flex: 1;
  display: flex;
  width: 100px;
  max-height: 50px;
  flex-wrap: wrap;
  padding: 10px;
  margin-bottom: 140px;
  margin-top: 80px;
  margin-left: 10px;
}
input {
  width: 30px;
  padding: 10px;
  margin: 0px 5px 5px 5px;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.6);
  color: rgba(0, 0, 0, 0.7);
  font-weight: bold;
  border-radius: 8px;
  border-color: white;
  box-shadow: white;
  text-align: center;
}

::placeholder {
  color: rgba(0, 0, 0, 0.274);
  font-weight: medium;
  font-size: 10px;
}

.buttonBox {
  margin-bottom: 20px;
}

.results {
  width: 100%;
  margin-top: 25px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
  text-align: left;
  margin-left: -20px;
  padding: 20px 20px 20px 20px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.3);
}

label {
  font-size: 0.8rem;
  font-weight: bold;
  display: grid;
  justify-content: left;
  padding-left: 8px;
  padding-bottom: 2px;
  margin-bottom: 0px;
  margin-top: 5px;
  color: white;
  justify-content: left;
}

.wrapper {
  white-space: nowrap;
}

.glass-button {
  display: inline-block;
  padding: 16px 32px;
  border: 0;
  text-decoration: none;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.5);
  border: 2px solid rgb(218, 218, 218);
  backdrop-filter: blur(30px);
  color: rgb(255, 255, 255);
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  text-transform: uppercase;
  margin-top: 30px;
  margin-bottom: 10px;
}

.glass-button:hover {
  background-color: rgba(100, 100, 100, 0.5);
  color: rgb(59, 59, 59);
}

.glass-button:active {
  background-color: rgba(37, 37, 37, 0.5);
  color: rgb(255, 255, 255);
}

.glass-buttonSave {
  display: inline-block;
  padding: 16px 32px;
  border: 0;
  text-decoration: none;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.1);
  border: 2px solid rgb(255, 255, 255);
  backdrop-filter: blur(30px);
  color: rgb(255, 255, 255);
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  text-transform: uppercase;
  margin-top: 30px;
  margin-bottom: 10px;
}

.glass-buttonSave:hover {
  background-color: rgba(100, 100, 100, 0.3);
  color: rgb(59, 59, 59);
}

.glass-buttonSave:active {
  background-color: rgba(37, 37, 37, 0.3);
  color: rgb(255, 255, 255);
}

.glass-buttonClear {
  display: inline-block;
  padding: 16px 32px;
  border: 0;
  text-decoration: none;
  border-radius: 15px;
  background-color: rgba(255, 255, 255, 0.5);
  border: 2px solid rgb(255, 255, 255);
  backdrop-filter: blur(30px);
  color: rgb(84, 84, 84);
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 2px;
  cursor: pointer;
  text-transform: uppercase;
  margin-top: 30px;
  margin-bottom: 10px;
}

.glass-buttonClear:hover {
  background-color: rgba(255, 255, 255, 0.2);
  color: rgb(59, 59, 59);
}

.glass-buttonClear:active {
  background-color: rgba(37, 37, 37, 0.5);
  color: rgb(255, 255, 255);
}
